@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&family=Open+Sans:wght@400;600&display=swap);
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
}
.App {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.main {
    font-family: 'Lato', sans-serif;
    font-size: 20px;
    font-weight: 700;
    width: 700px;
}

h2 {
    padding: 15px 0px 5px 0px;
    margin: 0px;
}

label, .label {
    display: inline-block;
    width: 130px;
    height: 29px;
    font-weight: 600;
    font-size: 20px;
    font-family: 'Open Sans', sans-serif;
}

.entry, .form-row>input, .form-row>textarea {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    font-size: 20px;
    display: inline-block;
}

input {
    width: 490px;
}

textarea {
    width: 510px;
}

input, textarea {
    background-color: #e7e7e7;
    border: none;
    border-radius: 2px;
}

input:hover, textarea:hover {
    outline: 1px solid rgba(10, 131, 207, 0.582);
    border-radius: 2px;
}

input:focus, textarea:focus {
    outline: 2px solid rgb(58, 171, 241);
}

.container {
    position: relative;
}

.control-buttons {
    visibility: hidden;
    position: absolute;
    top: 15px;
    right: 15px;
}

.container:hover>.control-buttons {
    visibility: visible;
}

.form-row, .entry-row {
    width: 700px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 5px 0px;
}

.section {
    border-bottom: 1px solid #555;
    padding-bottom: 15px;
}

.section:last-child {
    border-bottom: none;
}

.container {
    padding: 15px;
}

.control-buttons {
    display: flex;
    justify-content: flex-end;
}

.edit-button, .delete-button, .save-button {
    height: 40px;
    width: 40px;
    border: none;
    text-indent: -9999px;
    margin: 0px 3px;
}

.edit-button:hover , .delete-button:hover, .save-button:hover {
    cursor: pointer;
}

.edit-button {
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 30px;
}

.save-button {
    background: url(data:image/png;base64,) no-repeat center;
    background-size: 30px;
}


.delete-button {
    background: url(data:image/png;base64,) no-repeat center;
    background-size: 30px;
}

.add-button {
    margin: 15px 0px;
}

.info-button {
    position: relative;
    left: -15px;
}

.add-button, .info-button {
    background-color: rgb(255, 110, 58);
    padding: 5px;
    border: none;
    border-radius: 3px;
    font-size: 18px;
    color: white;
}

.add-button:hover, .info-button:hover {
    background-color: rgb(255, 156, 119);
    cursor: pointer;
}

.profile-container:hover>.profile-text {
    visibility: visible;
}

.header-container {
    background-color: rgb(0, 128, 255);
    width: 100vw;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.header {
    display: flex;
    color: white;
    font-family: 'Lato', sans-serif;
    width: 100vw;
    align-items: center;
    font-size: 20px;
    height: 70px;
    width: 700px;
    padding: 10px 0px;
}

.logo {
    height: 50px;
    padding-right: 10px;
}


.person-name, .person-email {
    width: 300px;
}

.person-phone {
    width: 190px;
}

.person-details {
    width: 430px;
}

.person-entry {
    width: 430px;
}

.person-entry>.form-row, .person-entry>.entry-row {
    width: 430px;
}

.information-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 0px;
    position: relative;
}
.profile-container {
    height: 140px;
    width: 140px;
    overflow: hidden;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
}

.profile-picture {
    width: 100%;
}

.profile-container:hover>.profile-picture{
    -webkit-filter: brightness(30%);
            filter: brightness(30%);
    cursor: pointer;
}

.profile-text {
    color: white;
    position: absolute;
    visibility: hidden;
}

.profile-text:hover {
    cursor: pointer;
}

#image-upload {
    display: none;
}
.school-name, .degree-name {
    width: 300px;
}

.school-start, .school-end {
    width: 190px;
}

.school-start-label {
    width: 130px;
}

.school-end-label {
    width: 120px;
    padding-left: 20px;
    margin-right: 20px;
}
.company-name, .job-title {
    width: 300px;
}

.job-description {
    width: 520px;
}

.job-start, .job-end {
    width: 190px;
}

.career-entry>:last-child {
    align-items: flex-start ;
}

.job-start-label {
    width: 130px;
}

.job-end-label {
    width: 120px;
    padding-left: 20px;
    margin-right: 20px;
}

